@use '@styles/abstracts/_variables.scss' as var;
@use '@styles/base/_mixins' as mixin;


.about-page {
  padding-bottom: 128px;

  @include mixin.wide-tablet {
    padding-bottom: 80px;
  }

  @include mixin.small-tablet {
    padding-bottom: 64px;
  }

  &__banner {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -43%);
      width: 59%;
      aspect-ratio: 1;
      pointer-events: none;

      @include mixin.small-tablet {
        display: none;
      }
    }
  }

  .info-list {
    margin-bottom: 128px;

    @include mixin.wide-tablet {
      margin-bottom: 80px;
    }

    @include mixin.small-tablet {
      margin-bottom: 64px;
    }
  }

  .info {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 48px;

    @include mixin.small-tablet {
      flex-wrap: wrap;
      gap: 32px;
    }

    &:first-child {
      margin-bottom: 32px;

      @include mixin.small-tablet {
        margin-bottom: 48px;
      }
    }

    &__title {
      font-family: var.$font-family;
      font-size: 3.2rem;
      font-weight: 300;
      line-height: 1.5;
      letter-spacing: 0.3px;
      margin-bottom: 16px;

      @include mixin.wide-tablet {
        font-size: 2.8rem;
      }

      @include mixin.small-tablet {
        font-size: 2.4rem;
      }
    }

    &__text {
      font-family: var.$font-family;
      font-size: 2.4rem;
      font-weight: 400;
      line-height: 1.66;

      @include mixin.small-laptop {
        font-size: 2rem;
      }

      @include mixin.tablet {
        font-size: 2rem;
      }

      @include mixin.small-tablet {
        font-size: 1.8rem;
      }
    }

    svg {
      flex-shrink: 0;
    }
  }

  .priority {
    margin-bottom: 128px;

    @include mixin.wide-tablet {
      margin-bottom: 80px;
    }

    @include mixin.small-tablet {
      margin-bottom: 64px;
    }

    &__title {
      margin-bottom: 48px;

      @include mixin.small-tablet {
        margin-bottom: 32px;
      }
    }

    &__list {
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      flex-wrap: wrap;
      gap: 30px;

      @include mixin.small-tablet {
        gap: 32px;
      }
    }

    &__item {
      flex-basis: 232px;
      flex-grow: 1;
    }

    &__img {
      max-width: 100%;
      border-bottom: 1px solid rgba(255, 255, 255, 0.20);
      margin-bottom: 34px;
      padding-bottom: 30px;

      @include mixin.wide-tablet {
        margin-bottom: 22px;
        padding-bottom: 22px;
      }

      @include mixin.small-tablet {
        border-bottom: unset;
        margin-bottom: 16px;
        padding-bottom: 0;
      }

      svg {
        max-width: 100%;
      }
    }

    &__text {
      font-family: var.$font-family;
      font-size: 1.8rem;
      font-weight: 300;
      line-height: 1.66;
      letter-spacing: 0.346px;
    }
  }

  .history {
    margin-bottom: 128px;
    position: relative;

    @include mixin.wide-tablet {
      margin-bottom: 80px;
    }

    @include mixin.small-tablet {
      margin-bottom: 64px;
    }

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -43%);
      width: 59%;
      aspect-ratio: 1;
      pointer-events: none;

      @include mixin.small-tablet {
        display: none;
      }
    }

    &__head {
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-wrap: wrap;
      margin-bottom: 48px;
      gap: 16px;

      @include mixin.small-tablet {
        margin-bottom: 32px;
      }
    }

    &__title {
      white-space: nowrap;
    }

    .history-slider {
      width: 100%;
      overflow: unset;

      &__body {
        position: relative;
      }

      &__control {
        display: flex;
        align-items: center;
        gap: 12px;
      }

      &__btn {
        display: flex;
        padding: 8px 16px;
        justify-content: center;
        align-items: center;
        border: 1px solid rgba(255, 255, 255, 0.10);

        &--left {
          border-top-left-radius: 16px;
          border-bottom-right-radius: 16px;
        }

        &--right {
          border-bottom-left-radius: 16px;
          border-top-right-radius: 16px;
          transform-origin: center;
          transform: rotateZ(180deg);
        }
      }
    }

    .history-item {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;

      &__year {
        text-align: center;
        font-family: var.$font-family;
        font-size: 2.4rem;
        font-weight: 400;
        line-height: 1.33;
        margin-bottom: 22px;
      }

      &__line {
        height: 12px;
        background: rgba(255, 255, 255, 0.20);
        width: 100%;
      }

      &__img {
        width: 72px;
        height: 82px;
        margin-top: -40px;
        margin-bottom: 12px;
      }

      &__text {
        font-family: var.$font-family;
        font-size: 1.8rem;
        font-weight: 300;
        line-height: 1.66;
        letter-spacing: 0.346px;
        padding-left: 36px;
        padding-right: 48px;

        @include mixin.small-tablet {
          padding-left: 25px;
          padding-right: 23px;
        }
      }
    }

    .swiper-slide {
      &:first-child {
        .history-item__line {
          border-top-left-radius: 10px;
          border-bottom-left-radius: 10px;
        }
      }

      &:last-child {
        .history-item__line {
          border-top-right-radius: 10px;
          border-bottom-right-radius: 10px;
        }

        .history-item__text {
          padding-right: 30px;
        }
      }
    }
  }

  .our-team {
    margin-bottom: 128px;

    @include mixin.wide-tablet {
      margin-bottom: 80px;
    }

    @include mixin.small-tablet {
      margin-bottom: 64px;
    }

    &__title {
      margin-bottom: 64px;

      @include mixin.small-tablet {
        margin-bottom: 32px;
      }
    }

    &__info-list {
      margin-bottom: 40px;

      @include mixin.small-tablet {
        margin-bottom: 0;
      }
    }

    &__info {
      margin-bottom: 24px;
      max-width: 522px;
      height: 100%;

      @include mixin.small-tablet {
        margin-bottom: 32px;
      }
    }

    &__info-title {
      font-weight: 500;
      letter-spacing: -1px;
      margin-bottom: 8px;

      @include mixin.phone {
        font-size: 2rem;
        line-height: 1.6;
      }
    }

    &__info-text {
      font-family: var.$font-family;
      font-size: 1.8rem;
      font-weight: 300;
      line-height: 1.66;
      letter-spacing: 0.346px;
    }

    &__images {
      margin-bottom: 64px;

      @include mixin.small-tablet {
        margin-bottom: 32px;
      }

      .row {
        gap: 24px 0;
      }

      .badge {
        overflow: hidden;
        height: 100%;
        padding: 0;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }

    &__text {
      font-family: var.$font-family;
      font-size: 1.8rem;
      font-weight: 300;
      line-height: 1.66;
      letter-spacing: 0.346px;
    }
  }

  .structure {
    &__title {
      margin-bottom: 16px;
    }

    &__subtitle {
      margin-bottom: 48px;

      @include mixin.small-tablet {
        margin-bottom: 32px;
      }
    }

    .structure-roles {
      display: flex;
      justify-content: space-between;
      align-items: stretch;
      flex-wrap: wrap;
      gap: 32px;
      margin-bottom: 32px;

      &__add {
        display: flex;
        align-items: stretch;
        flex-wrap: wrap;
        gap: 16px;
      }

      &__main {
        @include mixin.small-tablet {
          flex-grow: 1;
        }
      }
    }

    .structure-role {
      border-radius: 12px 0px;
      padding: 12px 24px;
      font-family: var.$font-family;
      font-size: 1.8rem;
      font-weight: 300;
      line-height: 1.66;
      letter-spacing: 0.346px;

      @include mixin.small-tablet {
        flex-grow: 1;
      }

      &--light {
        background: #F98500;
      }
    }

    .structure-slider {
      width: 100%;
      overflow: unset;

      @include mixin.small-tablet {
        display: none;
      }

      &__control {
        width: 100%;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        gap: 12px;
        margin-bottom: 32px;
      }

      &__body {
        width: 100%;
      }

      &__btn {
        display: flex;
        padding: 8px 16px;
        justify-content: center;
        align-items: center;
        border: 1px solid rgba(255, 255, 255, 0.10);

        &--left {
          border-top-left-radius: 16px;
          border-bottom-right-radius: 16px;
        }

        &--right {
          border-bottom-left-radius: 16px;
          border-top-right-radius: 16px;
          transform-origin: center;
          transform: rotateZ(180deg);
        }
      }
    }

    .structure-item {
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: stretch;
      padding-right: 80px;

      &__title {
        font-family: var.$font-family;
        font-size: 2.4rem;
        font-style: normal;
        font-weight: 400;
        line-height: 1.33;
        margin-bottom: 16px;
      }

      &__blocks {
        padding: 12px 24px;
        border-radius: 12px 0px;
        font-family: var.$font-family;
        font-size: 1.8rem;
        font-style: normal;
        font-weight: 300;
        line-height: 1.66;
        letter-spacing: 0.346px;
        margin-bottom: 16px;
        min-height: 114px;

        @include mixin.small-tablet {
          min-height: unset;
          font-size: 1.6rem;
          line-height: 1.5;
        }

        &:last-child {
          margin-bottom: 0;
        }

        &--head {
          min-height: auto;
        }
      }

      svg {
        width: 48px;
        position: absolute;
        top: 24px;
        right: 16px;
      }
    }
  }

  .mob-structure {
    display: none;

    &__dropdown {
      margin-bottom: 32px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    @include mixin.small-tablet {
      display: block;
    }

    &__dropdown-btn {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 10px;

      .title-4 {
        color: var.$light;
      }

      button {
        width: 48px;
        height: 48px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        transform-origin: center;
        transform: rotateZ(180deg);
        transition: 0.3s linear;
        flex-shrink: 0;

        svg {
          width: 16px;
          height: 9px;
        }

        path {
          fill: var.$light;
        }
      }

      &.collapsed button {
        transform: rotateZ(0deg);
      }
    }

    &__body {
      padding-top: 16px;
    }
  }
}

:not(.visually-impaired) {
  body.dark {
    background: #0A1121;

    .about-page {
      &__banner {
        &::before {
          background: radial-gradient(50% 50% at 50% 50%, rgba(249, 133, 0, 0.24) 0%, rgba(249, 133, 0, 0) 100%);
        }
      }
    
      .info {
        &__title {
          color: rgba(255, 255, 255, 0.50);
        }
    
        &__text {
          color: #FFF;
        }
      }
    
      .priority {
        &__text {
          color: #FFF;
        }
      }
    
      .history {
        &::before {
          background: radial-gradient(50% 50% at 50% 50%, rgba(249, 133, 0, 0.24) 0%, rgba(249, 133, 0, 0) 100%);
        }
    
        .history-item {
          &__year {
            color: #FFF;
          }
    
          &__line {
            background: rgba(255, 255, 255, 0.20);
          }
    
          &__text {
            color: #FFF;
          }
        }
      }
    
      .our-team {
        &__info-title {
          color: #FFF;
        }
    
        &__info-text {
          color: rgba(#FFF, 0.5);
        }

        &__text {
          color: #FFF;
        }
      }
    
      .structure {
        &__subtitle {
          color: #FFF;
        }
    
        .structure-role {
          background: #111D37;
          color: #FFF;
        }
        
        .structure-item {
          &__title {
            color: #FFF;
          }
    
          &__blocks {
            background: #111D37;
            color: #FFF;

            &--head {
              background: #F98500;
            }
          }
        }
      }
    
      .mob-structure {
        &__dropdown-btn {
          .title-4 {
            color: var.$light;
          }
    
          button {
            background: rgba(var.$light, 0.1);

            path {
              fill: var.$light;
            }
          }
        }
      }
    }
  }
}